@import "./src/assets/themes/appdb/variables.scss";

.search {
    &__result {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        gap: 15px;
        margin-top: 15px;
    }

    &__instruction,
    &__loading-spinner {
      margin-top: 15px;
    }

    &__loading-spinner {
      opacity: 0;

      &_visible {
        opacity: 1;
      }
    }
}
